<style lang="less" scoped src="../Project/index.less"></style>
<template>
    <div class='box'>
        <div class='title'>
            <span></span>
            <span class='left'>
                <div class='text1'></div>
            </span>
              <el-input class='query'></el-input>
              <el-button icon="el-icon-search" circle></el-button>
        </div>
        <template>
  <el-table :data="tableData"  border  style="width: 100%">
    <el-table-column  prop="address" label=""  align='center'> </el-table-column>
    <el-table-column  prop="project_name" label="" align='center'></el-table-column>
    <el-table-column  prop="address" label="" align='center'></el-table-column>
    <el-table-column  prop="address" label="" align='center'></el-table-column>
    <el-table-column  prop="developer_name" label="" align='center'></el-table-column>
    <el-table-column  prop="address" label="" align='center'></el-table-column>
    <el-table-column  prop="address" label="" align='center'></el-table-column>
    <el-table-column  prop="address" label="" align='center'></el-table-column>
    <el-table-column  prop="address" label="" align='center'></el-table-column>
    <el-table-column  prop="address" label="" align='center'></el-table-column>
    <el-table-column  prop="address" label="" align='center'></el-table-column>
    <el-table-column  prop="address" label="" align='center'></el-table-column>
    <el-table-column  prop="address" label="" align='center'></el-table-column>
     <el-table-column property="address" label="" align='center'>
        <template slot-scope="scope">
          <el-button type="text" @click='see'></el-button>
        </template>
      </el-table-column>
  </el-table>
</template>
    </div>
</template>
<script>
export default {
  data() {
    return {
        tableData:[],
    };
  },
  mounted(){
    this.getDistributionList();
  },
  methods:{
    async getDistributionList(){
      let res=await this.api.getDistributionList();
      if(res.code==200){
        this.tableData=res.data;
      }
    }
  }
  
};
</script>





