<style lang="less" scoped src="./index.less" ></style>
<template>
    <div class='box'>
        <div class='title'>
            <span></span>
            <span class='left'>
                <div class='text1'></div>
            </span>
              <el-input class='query'></el-input>
                <el-button icon="el-icon-search" circle></el-button>
        </div>
        <template>
  <el-table :data="tableData"  border  style="width: 100%">
    <el-table-column  prop="" label=""  align='center'> </el-table-column>
    <el-table-column  prop="project_name" label="" align='center'></el-table-column>
    <el-table-column  prop="address" label="" align='center'></el-table-column>
    <el-table-column  prop="address" label="" align='center'></el-table-column>
    <el-table-column  prop="company_name" label="" align='center' width="120px"></el-table-column>
    <el-table-column  prop="city_name" label="" align='center'></el-table-column>
    <el-table-column  prop="project_hold_name" label="" align='center'></el-table-column>
    <el-table-column  prop="project_hold_phone" label="" align='center' width="110px"></el-table-column>
    <el-table-column  prop="all_all_price" label="" align='center'></el-table-column>
    <el-table-column  prop="all_y_price" label="" align='center'></el-table-column>
    <el-table-column  prop="company_n_price" label="" align='center'></el-table-column>
    <el-table-column  prop="company_all_price" label="" align='center'></el-table-column>
    <el-table-column  prop="company_y_price" label="" align='center'></el-table-column>
    <el-table-column  prop="all_n_price" label="" align='center'></el-table-column>
    <el-table-column  prop="person_all_price" label="" align='center'></el-table-column>
    <el-table-column  prop="person_y_price" label="" align='center'></el-table-column>
    <el-table-column  prop="person_n_price" label="" align='center'></el-table-column>
    <el-table-column  label="" align='center'>
      <template slot-scope="scope">
        <el-button></el-button>
        <el-button></el-button>
      </template>
    </el-table-column>
  </el-table>
</template>
    </div>
</template>
<script>
export default {
  data() {
    return {
        tableData:[],
    };
  },
  mounted(){
    this.getProjectCommissionList();
  },
  methods:{
    async getProjectCommissionList(){
      let res=await this.api.getProjectCommissionList();
      if(res.code==200){
        this.tableData=res.data.data;
      }
    },
  }
};
</script>





